.card-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    width: 370px;
    height: 150px;
    padding: 15px;
    border-radius: var(--border-radius-value);
    background-color: var(--lite-color);
    box-sizing: border-box;
}

.card-client {
    display: flex;
    align-items: center;
    align-self: flex-start;
    gap: 10px;
}

.card-client img {
    width: 40px;
    height: 40px;
}

.card-client p {
    font-size: 24px;
    font-weight: bold;
}

.card-text {
    font-size: 20px;
}

@media screen and (max-width: 390px) {
    
    .card-block {
        padding: 10px;
        width: 320px;
    }
    
    .card-client p {
        font-size: 20px;
    }

    .card-block p {
        font-size: var(--lower-font-size);
    }
}