@font-face {
  font-family: "Fregat Regular";
  src: local("Fregat Regular"),
    url("../public/fonts/Fregat.woff2") format("woff2"),
    url("../public/fonts/Fregat.woff") format("woff");
  font-weight: normal;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Fregat Regular", sans-serif;
  margin: 0;
}


:root {
    --dark-color: #212121;
    --main-color: #E2A42B;
    --lite-color: #F9F9FB;
    --medium-font-size: 24px;
    --lower-font-size: 18px;
    --border-radius-value: 8px;
}

p, h1, h2, h3 {
    margin: 0;
}
