.container {
    width: 1200px;
    margin: 0 auto;
}

section, footer {
    padding: 50px 40px;
}

button {
    cursor: pointer;
}

a {
    cursor: pointer;
    text-decoration: none;
    color: inherit;
    transition: color 0.4s, text-decoration 0.4s;
}

a:hover {
    color: var(--main-color);
    text-decoration: underline;
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

h2 {
    font-size: 36px;
    font-weight: normal;
    margin-bottom: 30px;
}

.invalid {
    color: red;
    font-size: var(--lower-font-size);
}

header {
    background-color: #1C1C1C;
    color: var(--lite-color);
    margin-bottom: 150px;
}

.header-content {
    display: flex;
    justify-content: space-between;
}

.header-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.header-title {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
}

.header-title-main {
    background-color: var(--main-color);
    padding: 5px 100px;
}

.header-title h1 {
    font-size: 38px;
    filter: drop-shadow(3px 3px 0px rgba(0, 0, 0, 0.8));
}

.header-title p {
    font-size: 20px;
    color: var(--main-color);
}

/* ====================================================== */

.about-us-section {
    background-color: var(--main-color);
}

.about-us-block {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 40px;
}

.about-us-info {
    display: flex;
    flex-direction: column;
    gap: 50px;
    height: max-content;
    padding: 25px;
    background-color: var(--lite-color);
    border-radius: var(--border-radius-value);
}

.about-us-block img {
    width: 350px;
    height: 350px;
    border-radius: var(--border-radius-value);
    transition: transform 0.8s;
}

.about-us-block img:hover {
    transform: scale(1.05, 1.05);
}

.about-us-block p {
    text-indent: 60px;
    font-size: 20px;
}

/* ====================================================== */

.advantages-block {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.advantages-list {
    display: flex;
    flex-direction: column;
    gap: 22px;
}

.advantages-list li {
    font-size: var(--medium-font-size);
}

/* ====================================================== */

.works-section {
    background-color: var(--dark-color);
}

.works-block {
    display: flex;
    flex-direction: column;
}

.works-section h2 {
    color: var(--lite-color);
    align-self: center;
}

.works-list {
    display: flex;
    gap: 40px;
    overflow-x: scroll;
}

.works-list img {
    width: 350px;
    height: 350px;
    border-radius: var(--border-radius-value);
}

/* ====================================================== */

.make-order-section {
    background-color: var(--main-color);
}

.make-order-block {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.make-order-block h2 {
    color: var(--lite-color);
}

/* ====================================================== */

.VK-group-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.VK-group-block-info {
    display: flex;
    align-items: center;
    gap: 60px;
}

.VK-group-block-info img {
    width: 250px;
    height: 250px;
    transition: transform 0.8s;
}

.VK-group-block-info img:hover {
    transform: scale(1.08, 1.08);
}

.VK-group-block-info p {
    text-indent: 60px;
    font-size: var(--medium-font-size);
}

/* ====================================================== */

.feedback-section {
    background-color: var(--main-color); /*var(--dark-color)*/
    /* margin-bottom: 300px; */
}

.feedback-block {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.feedback-block h2 {
    color: var(--lite-color);
}

.feedback-block-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
}

.feedback-cards-list {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
}

form {
    width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    background-color: var(--lite-color);
    border-radius: var(--border-radius-value);
    padding: 20px 25px;
}

form h3 {
    font-size: 30px;
    font-weight: normal;
    color: var(--dark-color);
}

form input,
form textarea,
form select {
    font-size: var(--medium-font-size);
    border: none;
    border-radius: var(--border-radius-value);
    width: 450px;
    padding: 8px 15px;
    background-color: #D9D9D9;
    box-sizing: border-box;
    font-family: 'Fregat Regular';
}

form button {
    width: max-content;
    margin-top: 15px;
    padding: 8px 75px;
    color: var(--lite-color);
    background-color: var(--dark-color);
    font-size: var(--medium-font-size);
    border: 1px solid var(--lite-color);
    border-radius: var(--border-radius-value);
    transition: color 0.4s, background-color 0.4s, border 0.4s;
}

form button:hover {
    color: var(--dark-color);
    background-color: var(--lite-color);
    border: 1px solid var(--dark-color);
}

/* ====================================================== */

footer {
    background-color: var(--dark-color);
}

.footer-block {
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.footer-content-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.footer-content-block, .footer-copyright {
    color: var(--lite-color);
}

.footer-content-block p {
    font-size: 22px;
    margin-bottom: 8px;

}

.footer-copyright {
    display: flex;
    align-self: center;
    gap: 5px;
}

.footer-copyright img {
    width: 18px;
}

.footer-copyright p {
    font-size: 18px;
}


.css-19bb58m {
    padding: 0;
    margin: 0;
}

.css-hlgwow {
    padding: 0  ;
}

/* ====================================================== */

@media screen and (max-width: 1201px) {
	.container {
		width: 960px;
	}

    .header-content {
        justify-content: center;
    }

    .header-info {
        gap: 30px;
    }

    header img {
        display: none;
    }

    .about-us-block {
        flex-direction: column;
    }

    .about-us-block img {
        width: 70%;
        height: 70%;
    }
	
}

@media screen and (max-width: 993px) {
	.container {
		width: 720px;
	}

    section {
        padding: 50px 25px;
    }

    .about-us-block, 
    .footer-content-block, 
    .feedback-cards-list,
    .VK-group-block-info {
        flex-direction: column;
    }

    .about-us-block img {
        width: 100%;
        height: 100%;
    }

    .footer-content-block {
        gap: 30px;
    }

    .footer-content-block img {
        width: 400px;
    }
	
}

@media screen and (max-width: 769px) {
	.container {
		width: 540px;
	} 

    .header-info {
        flex-direction: row;
        margin-bottom: 30px;
    }

    .works-list {
        scrollbar-width: none;
    }
	
}

@media screen and (max-width: 391px) {
	.container {
		width: 100%;
	} 

    header {
        margin-bottom: 30px;
    }

    .header-info {
        gap: 15px;
    }

    .header-title-main {
        padding: 16px 35px;
    }

    .header-title h1 {
        font-size: 28px;
    }

    .header-title p {
        display: none;
    }

    h2 {
        font-size: 28px;
    }

    h3 {
        font-size: 18px;
    }

    .advantages-list li {
        font-size: 20px;
    }

    .works-list {
        scrollbar-width: none;
    }

    .works-list img {
        width: 250px;
        height: 250px;
    }

    .VK-group-block-info p {
        font-size: var(--lower-font-size);
    }

    form {
        padding: 15px;
        width: 300px;
    }

    form select,
    form input, 
    form textarea {
        width: 250px;
        font-size: var(--lower-font-size);
        padding: 8px 10px;
    }

    form button {
        font-size: var(--lower-font-size);
        padding: 8px 40px;
    }

    .footer-content-block img {
        width: 280px;
    }

    .footer-content-block p {
    font-size: 18px;
    }   	
}
